import React from 'react'
import {Form, FormGroup, Label, Input, Card} from 'reactstrap';

function Judges() {
    return (
        <div style={{color:"blue"}}>
            <Card style={{borderRadius:"0px", width:"80%"}}>
             <Form style={{padding:"3%"}}> 
      <FormGroup>
          <h2>Add a new judge</h2><br/>
        <Label for="name">NAME</Label>
        <Input style={{borderColor:"blue"}} type="text" name="Jname" id="jname" placeholder="" />
       <br/> <Label for="job">JOB TITLE/PORTFOLIO</Label>
        <Input style={{borderColor:"blue"}} type="text" name="job" id="job" placeholder="" />
        <br/> <Label for="company">COMPANY/ORGANIZATION</Label>
        <Input style={{borderColor:"blue"}} type="text" name="company" id="company" placeholder="" />
        <br/> <Label for="profile">PROFILE URL |E.G TWITTER, PERSONAL WEBSITE, LINKEDIN,ETC.|
</Label>
        <Input style={{borderColor:"blue"}} type="url" name="profile" id="profile" placeholder="" />
        <br/> <Label for="about">ABOUT(OPTIONAL)
</Label>
        <Input style={{borderColor:"blue", height:"150px"}} type="text" name="about" id="about" placeholder="" />
      
      </FormGroup>
      </Form>
      </Card>
        </div>
    )
}

export default Judges
